*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 60%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 59%;
  }
}
a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
}

ul {
  list-style: none;
}

body {
  box-sizing: border-box;
  position: relative;
  font-size: 1.6rem;
}

.wrapper {
  max-width: 100%;
  overflow-x: hidden;
  position: relative;
  z-index: 1;
}

section {
  background-color: #fafafa;
  position: relative;
}